<template>
<v-container>
  {{  isAffiliate  }}
  <v-card>
    <v-card-title><v-icon class="mx-2">mdi-chart-line</v-icon> Upsales Sales</v-card-title>
    <v-card-text class="px-6">
      <v-form ref="form"> 
        <v-row>
          <v-col cols="12" sm="5"  >
              <v-select  required :rules="[v => !!v || 'this field is required']" rounded outlined dense  label="Report" v-model="type" :items="types" item-text="text" item-value="value"></v-select>
          </v-col>
          <v-col cols="12" sm="7"  >
              <app-datepicker v-model="dates"></app-datepicker>
          </v-col>
          <v-col cols="12" :sm="isAffiliate ? 5 : 9" >
              <app-autocomplete  rounded outlined dense @input="setTag" v-bind="autocomplete.bind" v-model="autocomplete.value"></app-autocomplete>
          </v-col>
          <v-col cols="12" :sm="4" v-if="isAffiliate" >
              <app-autocomplete  rounded outlined dense
                ref="subaffiliate"
                v-model="subaffiliateValue"
                hideAddNew
                label="Subaffiliate"
                collection="subaffiliate"
                itemText="name"
                itemValue="_id"
                v-bind="subaffiliateBind"
              ></app-autocomplete>
          </v-col>

          <v-col cols="12" sm="3" >
              <v-btn large rounded color="green white--text" width="100%" @click="submit">Submit</v-btn>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
 
  </v-card>
   <v-data-table 
        class="my-5 elevation-1"
        :loading="loading"
        :headers="headers"
        :single-expand="singleExpand"
        :expanded.sync="expanded"
        show-expand
        item-key="_id"
        :items="items">
        <template v-slot:loading   >
            <v-skeleton-loader
            class="mt-6"
            height="94"
            type="list-item-three-line"
          >
          </v-skeleton-loader>
        </template>
    <template v-slot:item.amount="{ item }" >
        {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
    </template> 
     <template v-slot:item.total.amount="{ item }" >
        {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.total.amount) }}  
    </template>   
     <template v-slot:item.count="{ item }"  >
      {{ item.child.length }}
    </template>
    <template v-slot:item.deposits="{ item }"  >
            {{ getDeposits( item.child ) }}
    </template>
    <template v-slot:item.created_on="{ item }"  >
      {{ dateformat(new Date(item.created_on), 'yyyy-mm-dd') }}
    </template>
    <template v-slot:expanded-item="{ headers, item }">
      <td :colspan="headers.length" class="pa-4">
        <v-data-table :headers="dataHeaders" :items="item.child" @click:row="innerListClick($event, item)">
          <template v-slot:item.amount="{ item }" >
              {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
          </template> 
          <template v-slot:item.ftd.amount="{ item }" >
              {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.ftd.amount) }}  
          </template>   
           <template v-slot:item.total.amount="{ item }" >
              {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.total ? item.total.amount : 0) }}  
          </template> 
          <template v-slot:item.created_on="{ item }"  >
            {{ dateformat(new Date(item.created_on), 'yyyy-mm-dd') }}
          </template>
           <template v-slot:item.ftd.created_on="{ item }"  >
            {{ dateformat(new Date(item.ftd.created_on), 'yyyy-mm-dd') }}
          </template>
           <template v-slot:item.count="{ item }"  >
            {{ item.child.length }}
          </template>
           
        </v-data-table>
      </td>
    </template> 
      
  </v-data-table>
  <v-dialog v-model="dialog.value" scrollable max-width="960px">
      <v-card>
        <v-card-title v-html="dialog.title"></v-card-title>
        <v-divider></v-divider>
        <v-card-text style="height: 300px;">
          <v-data-table :headers="dialog.headers" :items="dialog.items">
          <template v-slot:item.amount="{ item }" >
              {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
          </template>   
         
          <template v-slot:item.created_on="{ item }"  >
            {{   dateformat(new Date(item.created_on), 'yyyy-mm-dd')   }}
          </template>
      
        </v-data-table>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn color="blue darken-1" text @click="dialog.value = false">Close</v-btn>
       
        </v-card-actions>
      </v-card>
    </v-dialog>

</v-container>

 
</template>
<script>
import AppAutoComplete from '../components/Autocomplete'
import AppDatePicker from '../components/DateRange'
import { mapActions } from 'vuex'
import dateformat from 'dateformat'


const leadHeaders = [
          {
            text: 'TP Account',
            align: 'start',
            value: 'customer.tpc',
            width: '10%'
        },
        {
          text: 'Account name',
          align: 'start',
          value: 'customer.name',
        },
        { text: 'Owner conversion', value: 'user.displayName' },

        { text: 'FTD', value: 'amount' },
        { text: 'Deposits', value: 'count' },
        { text: 'Total', value: 'total.amount' },
        { text: 'Date', value: 'created_on' }]

const affiliateHeaders = [
    
        {
          text: 'Affiliate',
          align: 'start',
          value: 'name',
        },
        { text: 'FTDs', value: 'count' },
        // { text: 'Deposits', value: 'total.counts'},
        // { text: 'Total', value: 'total.amount' },

        ]
const agentsHeaders = [
    
        {
          text: 'Agent',
          align: 'start',
          value: 'displayName',
        },
        { text: 'FTD', value: 'child.length' },
        // { text: 'Total', value: 'total.amount' }
        ]




// inner table
const dataHeadersLeads = [
        { text: 'Date', value: 'created_on' },
        { text: 'Amount', value: 'amount' },
        { text: 'Owner', value: 'user.displayName' }
      ]

const dataHeadersAffiliate = [
        {
            text: 'TP Account',
            align: 'start',
            value: 'customer.tpc',
            width: '10%'
        },
        {
          text: 'Account name',
          align: 'start',
          value: 'customer.name',
        },
        {
          text: 'Subaffiliate',
          align: 'start',
          value: 'subaffiliate.name',
        },
        { text: 'Owner conversion', value: 'user.displayName' },
        { text: 'FTD', value: 'amount' },
        { text: 'Deposits', value: 'count' },
        { text: 'Total', value: 'total.amount' },

        { text: 'Date', value: 'created_on' }
      ]
const dataHeadersAgent = [
         {
            text: 'TP Account',
            align: 'start',
            value: 'tpc',
        },
        {
            text: 'Account name',
            align: 'start',
            value: 'name',
        },
        //  {
        //     text: 'Affilaite',
        //     align: 'start',
        //     value: 'affiliate.name',
        // },
        { text: 'Owner conversion', value: 'ftd.user.displayName' },
         { text: 'FTD', value: 'ftd.amount' },
        { text: 'FTD Date', value: 'ftd.created_on' },
        { text: 'Deposits', value: 'child.length' },
        { text: 'Owner Total', value: 'total.amount' },
      ]
function pad(number, length) {
   
    var str = '' + number;
    while (str.length < length) {
        str = '0' + str;
    }

    return str;

}
export default {
  name: 'Report',
  components: {
    'app-autocomplete': AppAutoComplete,
    'app-datepicker': AppDatePicker
  },
  data() {
    let today = new Date();
    today.setHours(0, 0, 0, 0);
    const dates = [`${today.getFullYear()}-${pad(today.getMonth()+1, 2)}-01`, `${today.getFullYear()}-${pad(today.getMonth()+1, 2)}-${pad(today.getDate(),2)}`]
  
    return {
      loading: false,
      dateformat,
      dialog: {
        value: false,
        headers: dataHeadersLeads,
        items: [],
        title: ''
      },
      dataItems: [],
      dataHeaders: dataHeadersLeads,
      expanded: [],
      singleExpand: true,
      items: [],
      headers:leadHeaders,
      types: [
        { text: "By Lead", value: 'lead' },
        { text: "By Affiliate", value: 'affiliate' },
        { text: "By Agent", value: 'agent' }
      ],
      type: 'lead',
      autocomplete: {
        value: '',
        bind: {
          collection: 'customer',
          itemValue: '_id',
          itemText: 'name',
          hideAddNew: true,
          label: 'Select a lead',
          searchFields: ['name', 'tpc']
        }
      },
      subaffiliateValue: null,
      subaffiliateBind: {
        or: false,
        query: { affiliate: '!'}
      },
      dates 
    }
  },
  watch: {
    isAffiliate (vl) {
      if (!vl)
        this.subaffiliateValue = null
    },
    type (type) {
      this.items = []
       this.autocomplete.value = ''
      switch (type) {
        case 'lead':
          this.autocomplete.bind.collection = 'customer'
          this.autocomplete.bind.itemText = 'name'
          this.autocomplete.bind.label = 'Select a lead'
          this.autocomplete.bind.searchFields = ['name', 'tpc']
          delete this.autocomplete.bind.or 
          delete this.autocomplete.bind.query
   
          this.headers = leadHeaders
          this.dataHeaders = dataHeadersLeads
          this.items = []
          break;
        case 'affiliate':
          this.autocomplete.bind.collection = 'affiliate'
          this.autocomplete.bind.itemText = 'name'
          this.autocomplete.bind.label = 'Select an affiliate'
          
          
          delete this.autocomplete.bind.searchFields 
          delete this.autocomplete.bind.or 
          delete this.autocomplete.bind.query




          this.headers = affiliateHeaders
          this.dataHeaders = dataHeadersAffiliate
          this.items = []
          break;
        case 'agent':
          this.autocomplete.bind.collection = 'user'
          this.autocomplete.bind.itemText = 'displayName'
          this.autocomplete.bind.label = 'Select an agent'
       
          delete this.autocomplete.bind.searchFields 

          this.autocomplete.bind.or = false
          this.autocomplete.bind.query = {
              role: ['!wakel', '!affiliate']
          }


          this.headers = agentsHeaders
          this.dataHeaders = dataHeadersAgent
          
          this.items = []

          break;
      
      
      }
    }
  },
  computed: {
    isAffiliate () {
      return this.type == 'affiliate'
    }
  },
  methods: {
    ...mapActions({
      get: 'api/get'
    }),
 
    getDeposits(item) { // count deposits for affiliate
      const childs = item.map(e => e.child.length)
      return  childs.length ? childs.reduce((a,b) => a+b ) : 0
    },
    async innerListClick (item) {
      if (this.type == 'affiliate') {
       
         
        this.dialog.value = true
        this.dialog.items = item.child
        this.dialog.title = `( <small>${ item.customer.tpc }</small> ) ${item.customer.name}`

      }
      if (this.type == 'agent') {
           this.dialog.value = true
        this.dialog.items = item.child
        this.dialog.title = `( <small>${ item.tpc }</small> ) ${item.name}`


      }
    },
    setTag (evt) {
      if (this.isAffiliate) {
        this.$refs.subaffiliate.query.affiliate = evt ? evt : '!'
        if (!evt)
          this.subaffiliateValue = null
         

      }
    },
    async submit () {
      if (this.$refs.form.validate()) {
      
       let path;
       switch (this.type) {
        case 'lead':
          path = 'report'
          break;
        case 'affiliate':
          path = 'report/affiliate'
          break;
        case 'agent':
          path = 'report/agent'
          break;
      
      
      }
       
       
       this.loading = true
       const result = await this.get({ collection: path, query: { created_on: this.dates.join('|'), id: this.autocomplete.value || null, subaffiliate: this.subaffiliateValue } })
      
     
      
      if (result)
        this.items = result.data
       this.loading = false
       

      } else 
        this.$store.commit('error', 'form not valid')

    }
  }
}
</script>